import React from "react";
import "./CodeBlock.sass"
import copy from 'copy-to-clipboard';

const parser = typeof window != "undefined" ? new DOMParser() : null;

interface ICodeBlockProps
{
  formatted: string;
  footerhtml?: string;
  backCallback: (ev?) => any
}

export class HTMLContent extends React.Component<{className?: string, content: string}>
{
  render()
  {
    return (
      <div className={this.props.className} dangerouslySetInnerHTML={{__html: this.props.content}}>
      </div>
    )
  }
}

export class CodeBlock extends React.Component<ICodeBlockProps>
{
  code: string = ""
  formatted: string;

  on_copy_clicked = () => {
    copy(this.code, {
      format: "text/plain"
    })
  }
  
  on_copyhtml_clicked = () => {
    copy(this.formatted, {
      format: "text/html"
    })
    copy(this.formatted, {
      format: "text/plain"
    })
  }
  render() {
    this.formatted = this.props.formatted;
    let footerHtml = this.props.footerhtml != null ? Buffer.from(this.props.footerhtml, 'base64').toString('utf8') : null;
    let lines      = [];
    if(typeof window !== "undefined")
    {
      lines = this.formatted.split(/<\s*br\s*\/?\s*>/g).filter(l => {
        let n =  parser.parseFromString(`<span id="mark">${l}</span>`, "text/html").getElementById("mark")
        if(n != null && n.textContent != null)
        {
          this.code += n.textContent + "\n"
        }
        return n.firstChild != null && n.firstChild.textContent != ""
      })
    }


    return (
      <div className="code-block-container">
        <div className="code-block-table-container">
          <table>
            <tbody>
              <tr>
                <td className="line-numbers">
                  {lines.map((l,i) => <div className={`line-numbers code-line ${i%2 == 0 ? "code-line-1" : "code-line-2"}`} key={i}>{i+1}</div>)}
                </td>
                <td>
                  <div>
                    {lines.map((l,i) => <HTMLContent key={i} className={`code-line ${i%2 == 0 ? "code-line-1" : "code-line-2"}`} content={l}/>)}
                  </div>
                </td>
              </tr>
            </tbody>
          </table>
        </div>
        <div className="code-block-footer">
          {
            footerHtml != null ? <HTMLContent content={footerHtml}/> : ""
          }
          <span>
            <button className="button action-button" onClick={this.on_copy_clicked}>Copy Code</button>
          </span>
          <span>
            <button className="button action-button" onClick={this.on_copyhtml_clicked}>Copy HTML</button>
          </span>
          <span>
            <button className="button action-button" onClick={this.props.backCallback}>Back</button>
          </span>
        </div>
      </div>
    )
  }
}