import React from "react";
import { FormatError } from '../interfaces';
import { ToggleContent } from "./ToggleContent";

export class ErrorDisplay extends React.Component<{error: FormatError}>
{
  render()
  {
    let err = this.props.error;
    return(
      <div className="error-container">
        <div>
          {
            err.kind == "PARSER_ERROR" ? (
                  <span className="error">
                    Could not parse your M code. We use the <a href="https://github.com/microsoft/powerquery-parser">official microsoft powerquery-parser</a> so chances are that the issue is within your code.
                  </span>
            ) : ""
          }
          {
            err.kind == "CSS_ERROR" ? (
              <span className="error">Could not parse your inline CSS. We use <a href="https://github.com/csstree/validator">csstree-validator</a> for validation so chances are that the issue is within your css.</span>
            ) : ""
          }
          {
            err.kind == "INVALID_CONFIG" ? (
              <span className="error">Invalid configuration specified. Please check if your configuration matches the schema specified below.</span>
            ) : ""
          }
          {
            err.kind == "INVALID_REQUEST_BODY" ? (
              <span className="error">Invalid request body. This should not happen. Consider writing a bug report if the issue persists.</span>
            ) : ""
          }
        </div>
        <div>
          <ToggleContent head={(<span title="Information about the actual error that got thrown">Inner Error</span>)}>
            <code>
              {JSON.stringify(err.innerError, null, 3)}
            </code>
          </ToggleContent>
        </div>
        <div>
          <ToggleContent head={(<span title="Meta information about the error">Meta Info</span>)}>
            <code>
              {JSON.stringify(err.meta, null, 3)}
            </code>
          </ToggleContent>
        </div>
      </div>
    )
  }
}