import React from "react";
import { FaCaretRight, FaCaretUp } from "react-icons/fa";
import "./ToggleContent.sass";

export class ToggleContent extends React.Component<{head: any, toggled?: boolean, toggleDisplayOnly?: boolean}, {toggled: boolean}>
{
  constructor(props)
  {
    super(props);
    this.state = {
      toggled: this.props.toggled != null ? this.props.toggled : false
    }
  }

  onToggle = () => {
    this.setState({
      toggled: !this.state.toggled
    });
  }

  render()
  {
    return (
      <div className="toggle-content-container">
        <a onClick={this.onToggle}>
          {
            this.state.toggled == false ? <FaCaretRight></FaCaretRight> : <FaCaretUp></FaCaretUp>
          }
          {this.props.head}
        </a>
        {
          this.props.toggleDisplayOnly == true ? (
            <div className={`toggle-content-children ${this.state.toggled == false ? "no-display": ""}`}>
              {this.props.children}
            </div>
          ) : (
            this.state.toggled == true ? (
              <div className="toggle-content-children">
                {this.props.children}
              </div>
            ) : ""
          )
        }
      </div>

    )
  }
}